:root {
  --font-body: "Open Sans", sans-serif;
  --font-headings: "Poppins", sans-serif;
}

:root {
  --btn: hsl(322, 100%, 66%);
  --btn-hover: hsl(321, 100%, 78%);
  --message-error: hsl(0, 100%, 63%);
  --primary-color: hsl(192, 100%, 9%);
  --secondary-color: hsl(207, 100%, 98%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  min-height: 100vh;
  font-family: var(--font-body);
  color: var(--primary-color);
}

.container {
  width: 90%;
  max-width: 69.375rem;
  margin: 0 auto;
}

.buttons {
  display: block;
  width: 15rem;
  padding: 0.9375rem 0;
  margin: 0 auto;
  border-radius: 1.875rem;
  background-color: var(--btn);
  color: var(--secondary-color);
  text-align: center;
  font-weight: 700;
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.3);
  transition: all ease 0.4s;
}
.buttons:hover {
  opacity: 0.7;
}

h1,
h2,
h3 {
  font-family: var(--font-headings);
  font-weight: 700;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 1.875rem 0 6.25rem;
}
.header__logo {
  width: 8.125rem;
}
@media (min-width: 48em) {
  .header__logo {
    width: 8.75rem;
  }
}
.header__btn {
  color: var(--btn);
  font-weight: 600;
  padding: 0.3125rem 1.25rem;
  border: 1px solid var(--btn);
  border-radius: 1.875rem;
  box-shadow: 1px 1px 10px 0px rgba(255, 143, 216, 0.42);
  transition: all ease 0.4s;
}
.header__btn:hover {
  color: var(--btn-hover);
  border-color: var(--btn-hover);
}

.community__title {
  text-align: center;
}
@media (min-width: 48em) {
  .community__title {
    font-size: 2.5rem;
  }
}
.community__description {
  padding: 1.875rem 0;
  text-align: center;
  max-width: 31.25rem;
  margin: 0 auto;
}
.community__mockups {
  padding: 5.625rem 0;
}
.community__data {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 6.25rem;
}
.community__icon {
  width: 1.875rem;
}
.community__numbers {
  font-size: 3.75rem;
}
.community__info {
  display: block;
  text-align: center;
  color: #737373;
}